import React from 'react';
import { graphql } from 'gatsby';

import CreateTiles from '../components/common/createTiles';

import WorkNav from '../components/work/workNav';
import filterArticles from '../components/work/filterArticles';
import findTags from '../components/work/findTags';

const Work = ({ data, pageContext }) => {
  const allEntries = data.allSanityProject.nodes;
  const { discipline } = pageContext;
  const tags = findTags(allEntries);
  const tiles = filterArticles(allEntries, discipline);

  return (
    <section className="work">
      {tags && <WorkNav data={tags} />}
      {tiles && <CreateTiles data={tiles} />}
    </section>
  );
};

export default Work;

export const query = graphql`
{
  allSanityProject{
    nodes{
      disciplines
      categories
      title
      slug{
        current
      }
      tileImage{
        tileDescription
        asset{
          url
          fluid(maxWidth: 650) {
            src
            sizes
            srcSet
            srcWebp
            srcSetWebp
            aspectRatio
          }

          metadata{
            palette{
              dominant{
                background
              }
            }
          }
        }
      }
    }
  }
}
`;
